/* Estilos para el fondo y contenedor */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

* {
  box-sizing: border-box; 
}

.container {
  background-image: url("/src/components/imgs/homeBackground.png"); 
  background-size: cover;
  background-position: center;
  background-attachment: fixed; /* Mantener el fondo fijo en pantalla */
  height: 100vh; /* Ocupa el 100% de la altura de la ventana */
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  color: white;
  transition: filter 0.3s ease; /* Para hacer que el difuminado sea suave */
  padding-top: 50px; /* Añadir espacio en la parte superior */
}

/* Aplicar blur solo cuando el modal esté activo */
.container-modal-active {
  filter: blur(5px); /* Ajusta el nivel de difuminado según tus necesidades */
}

/* Estilo para el título */
.title {
  font-size: 5rem;
  margin-bottom: 20px;
  text-align: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

/* Imagen */
.image {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

/* Contenedor de los botones */
.buttons {
  display: flex;
  gap: 50px;
  margin-top: 20px;
}

/* Estilos de los botones */
.button {
  padding: 10px 20px;
  font-size: 2rem;
  background: none;
  border-color: white;
  border-width: 5px;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

/* Estilo al pasar el cursor sobre el botón */
.button:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

/* Estilos para la barra lateral */
.sidebar {
  position: fixed;
  top: 0;
  left: -250px; /* Fuera de la vista por defecto */
  width: 250px;
  height: 100%;
  background-color: #333;
  color: white;
  transition: left 0.3s ease; /* Efecto de deslizamiento */
  display: flex;
  flex-direction: column;
  padding-top: 60px;
  z-index: 999; /* Barra lateral al frente de todo */
}

.sidebar.open {
  left: 0; /* Cuando la barra lateral está abierta */
}

/* Estilos del botón para abrir/cerrar la barra lateral */
.sidebar-toggle {
  font-size: 20px;
  position: fixed;
  top: 20px;
  left: 67px;
  background-color: #333;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  z-index: 1000; /* Botón por encima de la barra lateral */
}

.sidebar-toggle:hover {
  background-color: #555; /* Efecto hover para el botón */
}

/* Contenido dentro de la barra lateral */
.sidebar-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Estilo de los botones dentro de la barra lateral */
.sidebar-button {
  background-color: #333; /* Mismo fondo que el botón de abrir/cerrar */
  color: white;
  padding: 10px 20px;
  margin: 10px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease; /* Suavizar transición del hover */
  width: 100%;
  text-align: center;
  font-size: 17px;
}

.sidebar-button:hover {
  background-color: #555; /* Cambia el color de fondo en hover */
}

/* Estilos del modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Los modales estarán debajo de la barra lateral */
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  width: 90%;
  max-width: 500px;
  text-align: left;
  position: relative;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  font-size: 20px;
}

/* Botón de información "i" en el centro superior */
.info-icon {
  position: fixed; /* Asegura que el botón esté siempre visible */
  top: 10px;
  left: 50%; /* Centrado horizontalmente */
  transform: translateX(-50%); /* Ajusta para centrar el botón en el medio */
  width: 50px;
  height: 50px;
  background-color: #36d399; /* Color de fondo */
  border-radius: 50%; /* Hacemos el botón circular */
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px; /* Tamaño de la letra */
  color: white; /* Color de la letra */
  font-family: Arial, sans-serif; /* Fuente */
  font-weight: bold;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Sombra para dar algo de profundidad */
  z-index: 1001; /* Asegura que esté por encima de otros elementos */
}

.info-icon:hover {
  background-color: #2bbf83; /* Color en hover */
  transform: translateX(-50%) scale(1.1); /* Escala el ícono ligeramente al pasar el ratón */
}

/* Estilo del modal de información */
.modal {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8); /* Fondo oscuro */
  justify-content: center;
  align-items: center;
  z-index: 1000; /* Asegura que el modal esté visible por encima de otros elementos */
}

.modal-content {
  background-color: #2c2c2c;
  padding: 30px;
  border-radius: 15px;
  color: #d4d4d4;
  width: 80%;
  max-width: 600px;
  text-align: center;
  position: relative;
}

.modal h2 {
  color: #36d399;
  margin-bottom: 20px;
}

.modal p {
  font-size: 1.2rem;
  color: #bbb;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 30px;
  cursor: pointer;
  color: #fff;
}

.close:hover {
  color: #ff4d4d;
  transform: rotate(90deg);
}

/* Contenedores de información para los cifrados */
.info-containers {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Ajusta las columnas automáticamente */
  gap: 20px; /* Espacio entre los contenedores */
  margin-top: 40px;
  padding: 0 20px; /* Añade espacio alrededor de los contenedores */
}

.info-container {
  background-color: #333; /* Fondo oscuro */
  padding: 20px;
  border-radius: 10px; /* Bordes redondeados */
  text-align: left;
  color: white;
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Animación de hover */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Sombra para dar profundidad */
}

.info-container:hover {
  transform: scale(1.05); /* Escala el contenedor al pasar el cursor */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4); /* Sombra más pronunciada al hacer hover */
}

/* Títulos de los contenedores */
.info-container h3 {
  color: #36d399; /* Color de título */
  margin-bottom: 15px;
  font-size: 1.5rem;
}

/* Descripción dentro del contenedor */
.info-container p {
  font-size: 1.1rem;
  margin-bottom: 15px;
  color: #bbb;
}

/* Botón dentro de los contenedores */
.info-container .button {
  background-color: #36d399; /* Color del botón */
  color: white;
  padding: 10px 20px;
  border: none;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 5px; /* Bordes redondeados en el botón */
  transition: background-color 0.3s ease;
}

.info-container .button:hover {
  background-color: #2bbf83; /* Color del botón en hover */
}

/* Estilos responsivos */
@media (max-width: 768px) {
  .container {
    padding: 10px;
    height: auto; /* Ajusta la altura */
    padding-top: 50px; /* Deja un espacio en la parte superior */
  }

  .title {
    font-size: 2.5rem;
    margin-top: 20px; /* Deja espacio arriba */
  }

  .image {
    width: 80px;
    height: 80px;
  }

  .sidebar {
    width: 200px;
  }

  .sidebar.open {
    left: 0;
  }

  .sidebar-toggle {
    left: 10px;
  }

  .button {
    font-size: 1.5rem;
    padding: 8px 16px;
  }

  .info-containers {
    grid-template-columns: 1fr; /* Mostrar los contenedores uno encima del otro */
  }

  .info-container {
    margin-top: 20px; /* Espacio entre los contenedores y el inicio de la página */
  }

  .info-container h3 {
    font-size: 1.2rem;
  }

  .info-container p {
    font-size: 1rem;
  }

  .info-icon {
    width: 40px;
    height: 40px;
    font-size: 24px;
  }
}
