/* General container styles */
.cifrador-container {
    max-width: 600px; /* Aumenta el ancho del contenedor */
    width: 100%;
    margin: 0 auto;
    padding: 30px; /* Aumenta el padding interno */
    border: 2px solid #2c2c2c; /* Bordes más oscuros */
    border-radius: 15px; /* Bordes más suaves y redondeados */
    background-color: #121212; /* Fondo negro profundo */
    box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.6); /* Sombra más pronunciada */
    font-family: 'Arial', sans-serif;
    color: #d4d4d4; /* Texto gris claro */
}

/* Title styling */
.cifrador-title {
    text-align: center;
    font-size: 2.5rem; /* Título más grande */
    color: #36d399; /* Verde oscuro brillante */
    margin-bottom: 25px;
    font-weight: bold;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6); /* Sombra de texto */
}

/* Input group styling */
.input-group {
    margin-bottom: 30px; /* Mayor separación entre grupos */
    display: flex;
    flex-direction: column;
}

.input-group label {
    margin-bottom: 10px;
    font-weight: bold;
    font-size: 1.4rem; /* Tamaño de texto más grande para los labels */
    color: #d4d4d4; /* Texto gris claro */
}

.input-field {
    padding: 15px; /* Aumenta el padding de los inputs */
    border-radius: 8px; /* Bordes redondeados más grandes */
    border: 2px solid #2c2c2c; /* Borde más grueso */
    background-color: #1c1c1c; /* Fondo oscuro */
    color: #d4d4d4; /* Texto gris claro */
    font-size: 1.4rem; /* Tamaño de texto más grande */
}

.input-field:focus {
    outline: none;
    border-color: #36d399; /* Verde oscuro en focus */
    box-shadow: 0 0 10px rgba(54, 211, 153, 0.5); /* Sombra en focus */
}

/* Slider styling */
.slider {
    -webkit-appearance: none; /* Elimina el estilo por defecto en WebKit (Chrome, Safari) */
    width: 100%;
    height: 12px; /* Ajusta la altura de la pista */
    background: #555; /* Color de la pista */
    border-radius: 6px; /* Bordes redondeados */
    outline: none;
    margin: 15px 0; /* Mayor margen */
}

.slider::-webkit-slider-thumb {
    -webkit-appearance: none; /* Elimina el estilo por defecto */
    appearance: none;
    width: 25px; /* Ajusta el tamaño del thumb */
    height: 25px;
    background: #36d399; /* Color del thumb (verde oscuro) */
    border-radius: 50%; /* Forma redonda */
    cursor: pointer;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5); /* Sombra en el thumb */
}

.slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: #36d399; /* Color del thumb */
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
}

.slider::-ms-thumb {
    width: 25px;
    height: 25px;
    background: #36d399; /* Color del thumb */
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
}

/* Button styles */
.info-btn {
    padding: 20px; /* Aumenta el padding del botón */
    width: 100%;
    background-color: #36d399; /* Verde oscuro personalizado */
    color: white;
    border: none;
    border-radius: 10px; /* Bordes más redondeados */
    cursor: pointer;
    margin-top: 15px;
    font-size: 1.4rem; /* Texto más grande */
    font-weight: bold;
    transition: background-color 0.3s ease, transform 0.2s ease;
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5); /* Sombra más pronunciada */
}

.info-btn:hover {
    background-color: #2bbf83; /* Verde más intenso en hover */
    transform: translateY(-3px); /* Efecto hover con movimiento */
}

.info-btn:focus {
    outline: none;
    box-shadow: 0 0 8px rgba(43, 191, 131, 0.6); /* Sombra verde en focus */
}

/* Modal styles */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9); /* Fondo más oscuro */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999;
    backdrop-filter: blur(5px); /* Desenfoque del fondo */
}

.modal-content {
    background-color: #1c1c1c; /* Fondo oscuro del modal */
    padding: 40px; /* Aumenta el padding del modal */
    width: 90%;
    max-width: 700px; /* Mayor tamaño del modal */
    border-radius: 15px; /* Bordes más suaves y redondeados */
    text-align: center;
    position: relative;
    color: #d4d4d4; /* Texto gris claro */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8); /* Sombra más intensa */
}

.modal h2 {
    font-size: 2rem; /* Título más grande en el modal */
    color: #36d399; /* Verde oscuro */
    margin-bottom: 25px;
}

.modal p {
    font-size: 1.4rem; /* Texto más grande en el modal */
    color: #bbb; /* Texto gris suave */
    line-height: 1.7;
}

.close {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 30px; /* Botón de cierre más grande */
    cursor: pointer;
    color: #fff; /* Botón de cerrar en blanco */
    transition: transform 0.3s ease;
}

.close:hover {
    color: #ff4d4d;
    transform: rotate(90deg); /* Rotación en hover */
}

/* Styling for the result text */
.result {
    margin-top: 25px;
}

.cifrado-text {
    font-size: 1.4rem; /* Texto más grande */
    color: #d4d4d4; /* Texto gris claro */
    background-color: #1e1e1e; /* Fondo oscuro */
    padding: 20px; /* Mayor padding */
    border-radius: 10px;
    word-wrap: break-word;
    border: 2px solid #444; /* Borde oscuro más grueso */
}

/* Accessibility - focus states */
button:focus, input:focus {
    outline: 2px solid #36d399;
    outline-offset: 4px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .cifrador-container {
        padding: 20px;
    }

    .modal-content {
        padding: 25px;
    }

    .info-btn {
        font-size: 1.2rem;
        padding: 15px;
    }
}
