/* General container styles */
.escitala-container {
    padding: 30px; /* Aumenta el padding */
    background-color: #121212; /* Fondo negro profundo */
    border-radius: 20px; /* Bordes más suaves y redondeados */
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.7); /* Sombra más pronunciada */
    max-width: 700px; /* Aumenta el ancho */
    margin: 50px auto; /* Más margen para mayor separación */
    text-align: center;
    font-family: 'Arial', sans-serif;
    color: #d4d4d4; /* Texto gris claro */
}

.escitala-title {
    font-size: 2.5rem; /* Título más grande */
    color: #36d399; /* Verde oscuro brillante */
    margin-bottom: 30px; /* Mayor separación debajo del título */
    font-weight: bold;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6); /* Sombra de texto */
}

.escitala-textarea {
    width: 100%;
    padding: 18px; /* Aumenta el padding */
    border-radius: 12px; /* Bordes más redondeados */
    border: 2px solid #2c2c2c; /* Borde oscuro */
    margin-bottom: 20px; /* Mayor separación entre elementos */
    font-size: 1.2rem; /* Texto más grande */
    background-color: #1c1c1c; /* Fondo casi negro */
    color: #d4d4d4; /* Texto gris claro */
    box-shadow: inset 0 3px 6px rgba(0, 0, 0, 0.6); /* Sombra interna */
}

.error-message {
    color: #ff6363; /* Rojo opaco para errores */
    font-weight: bold;
    margin-bottom: 20px; /* Mayor separación */
    font-size: 1.2rem; /* Texto más grande */
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.3); /* Sombra ligera en el texto */
}

.escitala-column-input {
    margin-bottom: 30px; /* Mayor separación */
}

.escitala-button {
    padding: 18px 30px; /* Aumenta el padding */
    border: none;
    border-radius: 10px; /* Bordes más redondeados */
    background-color: #36d399; /* Verde oscuro personalizado */
    color: #121212; /* Texto negro */
    cursor: pointer;
    font-size: 1.2rem; /* Texto más grande */
    transition: background-color 0.3s ease, transform 0.2s ease; /* Animación */
    margin-top: 25px; /* Mayor separación */
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5); /* Sombra para darle profundidad */
}

.escitala-button:hover {
    background-color: #2bbf83; /* Verde más brillante en hover */
    transform: scale(1.08); /* Aumenta ligeramente al hacer hover */
}

.copy-button {
    padding: 15px 25px; /* Aumenta el padding */
    border: none;
    background-color: #2bbf83; /* Verde oscuro más claro */
    color: #fff; /* Texto blanco */
    border-radius: 10px; /* Bordes redondeados */
    cursor: pointer;
    margin-top: 20px; /* Mayor separación */
    font-size: 1.2rem; /* Texto más grande */
    transition: background-color 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.4); /* Sombra */
}

.copy-button:hover {
    background-color: #1e9c66; /* Verde más intenso en hover */
    transform: scale(1.08); /* Efecto de crecimiento al hacer hover */
}

/* Modal styles */
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.9); /* Fondo casi negro */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    backdrop-filter: blur(5px); /* Efecto de desenfoque en el fondo */
}

.modal-content {
    background-color: #1c1c1c; /* Fondo oscuro */
    padding: 35px; /* Aumenta el padding */
    border-radius: 20px; /* Bordes más suaves */
    text-align: center;
    width: 90%;
    max-width: 10000px; /* Mayor tamaño del modal */
    position: relative;
    color: #d4d4d4; /* Texto gris claro */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.8); /* Sombra más intensa */
}

.close {
    position: absolute;
    top: 15px;
    right: 15px;
    font-size: 30px; /* Botón de cerrar más grande */
    cursor: pointer;
    color: #fff; /* Botón de cerrar en blanco */
    transition: transform 0.3s ease;
}

.close:hover {
    transform: rotate(90deg); /* Efecto de rotación al hacer hover */
}

.result {
    margin-top: 30px; /* Mayor separación */
}

.ciphered-text, .deciphered-text {
    font-size: 1.4rem; /* Texto más grande */
    background-color: #2c2c2c; /* Fondo gris oscuro */
    padding: 22px; /* Aumenta el padding */
    border-radius: 12px; /* Bordes más grandes */
    border: 2px solid #3e3e3e; /* Borde gris más oscuro */
    word-wrap: break-word;
    color: #d4d4d4; /* Texto gris claro */
    box-shadow: inset 0 2px 6px rgba(0, 0, 0, 0.7); /* Sombra interna profunda */
}

/* Slider styling */
.escitala-slider {
    -webkit-appearance: none; /* Elimina el estilo por defecto en WebKit */
    width: 100%;
    height: 12px; /* Aumenta la altura de la pista */
    background: #555; /* Color de la pista */
    border-radius: 6px;
    outline: none;
    margin: 15px 0;
}

.escitala-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 25px; /* Aumenta el tamaño del thumb */
    height: 25px;
    background: #2ac057; /* Color del thumb (verde oscuro) */
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.6); /* Sombra en el thumb */
}

.escitala-slider::-moz-range-thumb {
    width: 25px;
    height: 25px;
    background: #2ac057;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
}

.escitala-slider::-ms-thumb {
    width: 25px;
    height: 25px;
    background: #2ac057;
    border-radius: 50%;
    cursor: pointer;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
}

/* Cambia el color de la pista activa (mientras se arrastra el slider) */
.escitala-slider::-webkit-slider-runnable-track {
    background: #444;
}

.escitala-slider::-moz-range-track {
    background: #444;
}

.escitala-slider::-ms-track {
    background: transparent;
    border-color: transparent;
    color: transparent;
}

.escitala-slider::-webkit-slider-thumb:active {
    background-color: #218838; /* Color más oscuro al hacer click */
}

.escitala-slider::-moz-range-thumb:active {
    background-color: #218838;
}

.escitala-slider::-ms-thumb:active {
    background-color: #218838;
}
